// APP => 
// ---------------------------------------- [START][Project] Hide APP
%hide-app {
	$el: &;

	//  <html>  .app  class 
	// =>  APP display block
	@at-root html:not(.app) {
		#{$el} {
			display: none;
		}
	}
}
// ---------------------------------------- [END][Project] Hide APP

// --------------------------------------------------------

.hd-app {
	@extend %hide-app;
}

// Product List
// 
.pd-list-banner {
	@extend %hide-app;
}

// 
.pd-ask__bottom,
.ask-input {
	@extend %hide-app;
}
